export default {
	data() {
		return {};
	},
	methods: {
		formSubmit(e) {
			var me = this;
			var username = e.detail.value.username;
			var email = e.detail.value.email;
			var password = e.detail.value.password;
			var repassword = e.detail.value.repassword;
			// 				console.log(username);
			// 				console.log(password);
			if (username == '') {
				uni.showModal({
					content: '手机号不能为空，请重新输入',
					showCancel: false
				});
				return false;
			}
			if (username.length != 11) {
				uni.showModal({
					content: '请正确输入手机号',
					showCancel: false
				});
				return false;
			}
			if (email == '') {
				uni.showModal({
					content: '邮箱不能为空，请重新输入',
					showCancel: false
				});
				return false;
			}
			if (password == '') {
				uni.showModal({
					content: '密码不能为空，请重新输入',
					showCancel: false
				});
				return false;
			}
			if (repassword == '') {
				uni.showModal({
					content: '确认密码不能为空，请重新输入',
					showCancel: false
				});
				return false;
			}
			if (repassword != password) {
				uni.showModal({
					content: '两次密码不一样，请重新输入',
					showCancel: false
				});
				return false;
			}
			// 发起注册/登录的请求
			var serverUrl = me.serverUrl;
			uni.request({
				url: serverUrl + '/re_login',
				data: {
					username: username,
					email: email,
					password: password
				},
				method: 'POST',
				success: res => {
					// 获取真实数据之前，务必判断状态是否为200
					console.log(res.data.code);
					if (res.data.code == 200) {
						var userInfo = res.data.data;
						// console.log(userInfo);
						// 保存用户信息到全局的缓存中
						uni.setStorageSync('globalUser', userInfo);
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
						});
						// 切换页面跳转，使用tab切换的api
						uni.switchTab({
							url: '../user/user'
						});
					} else if (res.data.code == 201) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							image: '../../static/icon/error.png'
						});
					} else if (res.data.code == 202) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							image: '../../static/icon/error.png'
						});
					} else if (res.data.code == 203) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							image: '../../static/icon/error.png'
						});
					}
				}
			});
		}
	}
};
